import {cancelableFetch} from "./cancelable-fetch";

/**
 * @param array
 * @param chunk_size
 * @returns []
 */
function array_to_chunks(array, chunk_size) {
    return array.reduce((chunks, value, index) => {
        const chunkIndex = Math.floor(index / chunk_size);
        if (!chunks[chunkIndex]) {
            chunks[chunkIndex] = [];
        }
        chunks[chunkIndex].push(value);
        return chunks;
    }, []);
}

/**
 * Get batch api options.
 *
 * @returns Promise
 */
export function fetchBatchOptions() {
    return cancelableFetch({
        path: '/batch/v1',
        method: 'OPTIONS'
    })
}

export async function batchApiRequest(limit, data, requestGenerator, requestHandler) {

    const chunks = array_to_chunks(data, limit);
    for (const chunk of chunks) {
        const requests = [];

        for (const item of chunk) {
            requests.push(requestGenerator(item));
        }

        const batchRequest = cancelableFetch({
            path: '/batch/v1',
            method: 'POST',
            data: {
                requests
            }
        });

        if (requestHandler) {
            requestHandler(batchRequest);
        }

        await batchRequest;
    }
}

